div.mainWrapper {
  width: 100vw;
  height: 10vh;
}

div.mainTitle {
  font-size: 2rem;
  text-align: center;
  padding-top:  1rem;
  padding-bottom: 1rem;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.titleCenter {
  width: 60vw;
}

.loginWrapper {
  margin-top: 2rem;
  width: 100vw;
  min-height: 400px;
  background-color: #fafafa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.loginForm {
  padding-top: 1rem;
  width: 60vw;
  min-height: 100px;
  background-color: white;
  border: solid 1px #d2d2d2;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*justify-content: center;*/
  -ms-flex-align: center;
      align-items: center;
}

.login_button {
  width: 50%;
  height: 45px;
  background-color: rgb(0,199,60);
  color: white;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5rem;
  cursor: pointer;
  margin-top: 1rem;
}


.selectAge {
  width: 50%;
  height: 45px;
  font-size: 1.5rem;
  background-color: white;
  border-color: rgb(0,199,60);
  margin-bottom: 20px;
}


.selectGender {
  width: 50%;
  height: 45px;
  margin-bottom: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Men {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5rem;
  width: 45%;
  cursor: pointer;
  border-radius: 10px;
  border: 1px solid rgb(0,199,60);
}

.Women {
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid rgb(0,199,60);
  width: 45%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5rem;
}

.SelectedMen {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5rem;
  width: 45%;
  cursor: pointer;
  border-radius: 10px;
  border: 1px solid rgb(0,199,60);
  background-color: rgb(0,199,60);
  color: white;
}

.SelectedWomen {
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid rgb(0,199,60);
  width: 45%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5rem;
  background-color: rgb(0,199,60);
  color: white;
}

.loginTitle {
  width: 50%;
  height: 45px;
  margin-bottom: 2.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.5rem;
  border-bottom: 1px solid #ebebeb;
  margin-top: 1rem;

}

.loginError {
  color: red;
  text-align: center;
  margin-top: 10px;
}

@media screen and (max-width: 500px) {
  * {
    font-size: 10px;
  }

  .loginForm {
    width: 80%;
  }

  .loginTitle {
    width: 80%;
  }

  .selectGender {
    width: 80%;
  }

  .selectAge {
    width: 80%;
  }

  .login_button {
    width: 80%;
  }

  .loginWrapper {
    min-height: 300px;
  }
  .titleCenter {
    width: 80%;
    font-size: 1.2rem;
  }
}



