.selectWrapper {
  width: 100vw;
  /*min-height: 500px;*/
  background-color: #fafafa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 16px;
  padding-bottom: 16px;
}

.selectNav {
  width: 15vw;
  text-align: center;
  cursor: pointer;
}

.selectColumn {
  width: 60vw;
  background-color: white;
  border: solid 1px #d2d2d2;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  padding-top: 2rem;
  padding-bottom: 2rem;
  -ms-flex-align: center;
      align-items: center;
}

.selectBox {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.selectImg {
  width: 30vw;
  height: 30vw;
  border-radius: 20px;
  overflow: hidden;
  background: green;
}

.titleImage {
  width: 100%;
  height: 100%;
}

.selectName {
  /*width: 50%;*/
  width: 100%;
  border-bottom: 1px solid #ebebeb;
  text-align: center;
  font-size: 1.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

.selectFilter {
  text-align: center;
  width: 100%;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
}

.selectIcon {
  width: 3rem;
  height: 3rem;
}

.selectEmpty {
  width: 100%;
  text-align: center;
  margin-bottom: 1.5rem;
}

.selectEmpty2 {
  width: 100%;
  text-align: center;
  margin-bottom: 1.5rem;
}

.goAddress {
  width: 100%;
  margin-top: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.goBtn {
  width: 50%;
  height: 45px;
  background-color: yellow;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 10px;
  background-color: rgb(0,199,60);
  color: white;
  cursor: pointer;
}

.itemGrid {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.goHome {
  cursor: pointer;
  font-size: 1.5rem;
}
.CharWrapper {
  width: 100vw;
  background-color: #fafafa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:  column;
      flex-direction:  column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.ChartTitle {
  font-size: 1.2rem;
  font-weight: bold;

}

.ChartBox {
  width: 100%;
  height: 100%;
}


@media screen and (max-width: 500px) {
  .goBtn {
    height: 25px;
  }
}



