.categoryForm {
  padding-top: 1rem;
  padding-bottom: 1rem;
  width: 80vw;
  min-height: 100px;
  background-color: white;
  border: solid 1px #d2d2d2;
  border-radius: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*justify-content: center;*/
  -ms-flex-align: center;
      align-items: center;
}

.itemWrapper {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /*justify-content: space-around;*/
  /*flex-direction: column;*/
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.Category-Wrapper {
  width: 100%;
  height: 100%;
}

.itemGrid {
  width: 100%;
  height: 160px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;

}

.Category-box {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 135px;
  height: 135px;
  border-radius: 50%;
  overflow: hidden;
  /*display: flex;
  justify-content: center;
  align-items: center;*/
  cursor: pointer;
  border: 2px solid #ebebeb;
  position: relative;
}

.CategoryTitle {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  left: 0;
  top: 0;
  width: 135px;
  height: 135px;
}

.categoryImg {
  opacity: 0.2;
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.bb {
  width: 100%;
  height: 100%;
}

.goHome {
  width: 50%;
  height: 45px;
  background-color: rgb(0,199,60);
  border-radius: 10px;
  margin-bottom: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: white;

}

@media screen and (max-width: 500px) {
  .Category-box {
    font-size: 1.5rem;
    font-weight: bold;
  }
}



